import React, { useState, useEffect } from "react";
import Layout from "components/layout";
import InlineScriptLoader from "utils/addScript";

const SecureFileUpload = ({ location }) => {
  const [areScriptsInit, setScriptsInit] = useState(false);

  const checkScriptInit = () => {
    let scriptPollingInterval;
    if (window.jQuery && window.SendSafelyDropzone) {
      setScriptsInit(true);
      clearInterval(scriptPollingInterval);
    }
  };

  return (
    <Layout location={location} whiteContentBackground whiteGutterBackground>
      <InlineScriptLoader
        scriptId="sendSafelyDropzone"
        scriptSrc="https://app.sendsafely.com/js/external/SendSafelyDropzone.min.js"
        removeOnUnmount
      />
      <InlineScriptLoader
        scriptSrc="https://code.jquery.com/jquery-3.3.1.min.js"
        scriptId="jQuery"
        removeOnUnmount
        windowObject="jQuery"
        callback={() => setInterval(checkScriptInit, 500)}
      />
      <InlineScriptLoader
        scriptSrc="https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/js/bootstrap.min.js"
        scriptId="bootstrap"
        removeOnUnmount
        windowObject="bs"
      />

      {areScriptsInit && (
        <>
          <InlineScriptLoader
            scriptId="sendSafelyMethods"
            scriptText={formScript}
            removeOnUnmount
          />
        </>
      )}

      <div
        dangerouslySetInnerHTML={{
          __html: `
<div align="Center">
<IMG style="height:61px;margin-top:20px;margin-bottom:10px;" SRC="https://brex.com/logo_dark_250px.png"><br>
<H4 style="color: #000;">Secure File Upload</H4>
</div><br>
<div class="card" style="padding:20px; margin:auto; max-width:440px; color: #000;">
<form id="dropzone_form" name="dropzone_form">
<div class="form-group">
<label for="name" style="color: #000;">Company Name</label> <input class="form-control" id="company-name" name="company-name" placeholder="Enter your company name" type="text">
</div>
<div class="form-group">
<label for="email" style="color: #000;">Brex Email Address</label> <input type="email" class="form-control" id="email" name="email" placeholder="Enter your Brex email address">
</div>
<div class="form-group" style="display: none">
<input type="text" id="reference" name="reference" value="${location.search.substring(
            1,
          ) || null}"></div>
<div class="form-group">
<div id="dropzone-placeholder-div" style="color: #000;"></div>
</div>
<div align="center">
<button class="btn btn-primary" id="sendsafely-submit" onclick="submitForm()" type="button" style=background-color:#ff6938>Submit <i class="fas fa-spinner fa-spin" id="spinner" style="display:none; margin-left:5px;"></i></button>
</div>
</form>
<div id="submit_done" style="padding:10px; display:none">
<br>
<div align="center">
<i style="color:#ff6938; font-size:84px;" class="fas fa-check-circle"></i> </div><br><br>
<div align="center">
<b style="color: #000;">Thank you. A Brex Support Team member will be in touch if further action is needed.</b><br>
<br>


<br><b style="color: #000;">Your submission has been recorded. For your personal reference, your submission ID# is</b> <span id="dropzone-thread-id" style="color: #000;">N/A</span><br>
<br>


</div>


<br>
</div>
</div>
<div style="padding:50px" align="center"><IMG height="40" width="181" src="https://sendsafely.s3.amazonaws.com/images/secured_with_sendsafely_181x40.png"></div>

        `,
        }}
      />
    </Layout>
  );
};

export default SecureFileUpload;

const formScript = `function isFormValid() {
var isValid = true;
if (!/(.)@(.){2,}\.(.+){2,}/.test($('#email').val())) {
alert('You did not provide a valid email address');
isValid = false;
return isValid;
}
$('#dropzone_form input[type=text]').each(function() {
if (isValid && this.value.trim() == '') {
alert('You must complete all input fields');
isValid = false;
return isValid;
}
});
if (isValid && widget.nbrOfFilesAttached < 1) {
alert('You did not attach a file');
isValid = false;
return isValid;
}
return isValid;
}

function submitHostedDropzone(url) {
var postData = {};
postData['name'] = url_identifer;
postData['email'] = $('#email').val();
postData['packageCode'] = widget.packageCode;
postData['publicApiKey'] = dropzoneId;
$.post(
sendSafelyHost + '/auth/json/?action=submitHostedDropzone',
postData,
function(result) {
if (result.integrationUrls !== undefined) {
for (i = 0; i < result.integrationUrls.length; i++) {
var integrationUrl = result.integrationUrls[i];
//Third party form integration...do post to URL
var postData = {};
postData['digest'] = result.digest;
postData['data'] = result.data;
postData['secureLink'] = url;
$('#dropzone_form input[type=text]').each(function() {
postData[this.name] = this.value;
});
$.post(
integrationUrl,
postData,
function(json) {
if (json.error != undefined) {
alert(json.error);
$('#spinner').hide();
} else {
//success
$('#dropzone_form').hide();
$('#submit_done').show();
}
},
'json'
);
}
}
}
);
}

function submitForm() {
if (isFormValid()) {
$('#spinner').show();
widget.setUnconfirmedSender(
$('#email')
.val()
.toLowerCase()
);
widget.finalizePackage(
function(url) {
var threadRegex = new RegExp('thread=[A-Za-z0-9-]+');
var threadId = threadRegex
.exec(url)
.toString()
.substr(7);
$('#dropzone-thread-id').text(threadId);
submitHostedDropzone(url);
},
function() {
$('#spinner').hide();
}
);
}
}

var el = document.createElement('a');
el.href = window.location;
var url_identifer = el.pathname.split('/')[el.pathname.split('/').length-1];
var widget;
var sendSafelyHost = 'https://brex.sendsafely.com';
var dropzoneId = '1ywP6CAxIRi4hBV4O9aj0PO9HnwpO-BYlap--15bojQ';
$(document).ready(function() {
var placeholderElement = $('#dropzone-placeholder-div');
var formField = $('#sendsafely-link');
widget = new SendSafelyDropzone(dropzoneId, placeholderElement, formField);
widget.url = sendSafelyHost;
widget.initialize();
});`;
